import React, { Fragment } from 'react';
import DataRender from './DataRender';
import github from '../apis/github';

class SearchForm extends React.Component {
    constructor(props) {
        super(props)
        this.input = React.createRef();
    }
    
    state = { input: null, data: {} };

    componentDidUpdate = async (prevProps, prevState) => {

        if(Object.keys(this.state.input).length > 0){
            if  (prevState.input !== this.state.input ){
                let response = {};
                try {
                    response = await github.get(`/${this.state.input}`);
                    this.setState({ data: response.data });
                } catch(err) { 
                    response = { data: err };
                    this.setState({ data: response.data });
                }
            }
            this.input.current.value = '';
        }
    };

    onFormSubmit = (event) => {
        event.preventDefault();
        this.setState({ input: this.input.current.value });
    }

    render() {
        return (
            <Fragment>
                <form onSubmit={this.onFormSubmit} className="form">
                    <div className="form__input">
                    <input 
                        ref={this.input}
                        type="text"
                        className="form__input--txt"
                        placeholder="Type username..."
                    />
                    </div>
                    <div className="form__btn">
                    <input
                        type="submit"
                        value="Search"
                        className="form__input--btn"
                    />
                    </div>
                </form>
                <DataRender user={this.state.data} />
            </Fragment>
        );
    }
}

export default SearchForm;